body {
  position: relative;
  min-height: 1775px;
  padding-top: 33px;
  background: url(../img/background-body-img.png) no-repeat, url(../img/background-body.jpg);
  background-size: 100%, auto;
  font-family: georgia;
  font-weight: normal;
  font-style: italic;
  font-size: 12px;
  line-height: 1.5;
  color: #2b5e5e;
}

@media only screen and (max-width: 1024px) {
  body {
    padding-top: 0;
  }
}

@media only screen and (max-width: 900px) {
  body {
    min-height: 2000px;
  }
}

