body {
	position: relative;
	min-height: 1775px;
	padding-top: 33px;
	background: url(../img/background-body-img.png) no-repeat, url(../img/background-body.jpg);
	background-size: 100%, auto;
	font-family: georgia;
	font-weight: normal;
	font-style: italic;
	font-size: 12px;
	line-height: 1.5;
	color: $blue_text;
	@include r(1024) {
		padding-top: 0; }
	@include r(900) {
		min-height: 2000px; }
	@include r(320) {
		min-height: 4200px; } }

.wrapper {
	width: 1000px {
  margin: 0 auto; }
	margin-left: auto;
	margin-right: auto;
	@include r(1024) {
		width: 100%; } }

.container {
	width: 940px;
	margin: 0 auto; }
.container {
  @include r(900) {
    width: 840px; } }
.container {
	@include r(768) {
		width: 725px; } }
.container {
	@include r(320) {
		width: 280px; } }

.logo-bottom {
	position: absolute;
	margin-left: 0;
	bottom: 150px; }

.btn-nav {
	margin-left: auto; }
