.main-nav {

  // +r(768)
  //   display: none

  //   &.active
  //     display: block

  &__list {
    display: flex;
    @include r(320) {
      flex-direction: column;
      align-items: center; } }

  &__item {
    margin-right: 17px;
    @include r(900) {
      margin-top: 5px; }
    @include r(768) {
      margin-right: 15px; } }

  &__link {
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-size: 18px;
    line-height: 1.2;
    color: $white_text;
    text-decoration: none;
    text-transform: uppercase;
    @include r(900) {
      font-size: 17px; }
    @include r(768) {
      font-size: 16px; } } }

