.cards {

  &__list {
    display: flex;
    justify-content: space-between;
    @include r(320) {
      flex-direction: column;
      align-items: center; } }

  &__item {
    display: flex;
    flex-wrap: wrap;
    width: 170px;
    padding: 18px;
    padding-bottom: 0;
    background-color: $white;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.14);
    @include r(320) {
      margin-bottom: 10px; } }

  &__title {
    margin-top: 15px;
    margin-bottom: 8px;
    font-style: normal;
    text-decoration: underline; }

  &__revievs {
    display: flex;
    margin-left: auto;
    font-size: 11px;
    color: #757575;
    &-icon {
      margin-right: 5px; } } }
